@import url(//fonts.googleapis.com/earlyaccess/notosanstc.css);
.ant-table-thead > tr > th,
.ant-table-tbody > tr > td {
  padding: 6px 16px; }

.ant-table-thead > tr > th {
  background-color: #fff; }

.header {
  width: 18%;
  max-height: 600px;
  overflow-y: scroll;
  padding: 5px;
  float: left;
  position: fixed;
  -webkit-font-smoothing: subpixel-antialiased; }
  .header .block {
    margin: 0 0 20px; }

.avatar {
  max-width: 100%; }

.section {
  width: 80%;
  float: right;
  padding-bottom: 50px; }

.footer {
  width: 80%;
  float: left;
  position: fixed;
  bottom: 0;
  -webkit-font-smoothing: subpixel-antialiased; }

@media screen and (max-width: 960px) {
  .header,
  .section,
  .footer {
    float: none;
    position: static;
    width: auto; }
  .wrapper {
    width: auto;
    margin: 0; }
  .header {
    padding-right: 320px; }
  .section {
    padding: 20px 0;
    margin: 0 0 20px; } }

@media screen and (max-width: 720px) {
  .header {
    padding: 0; } }

body {
  background-color: #fff;
  padding: 15px;
  font: 14px/1.5 "Noto Sans TC", sans-serif;
  color: #595959;
  font-weight: 400; }

::-webkit-scrollbar {
  width: 1px;
  height: 1px; }

::-webkit-scrollbar-thumb {
  background-color: #e6e6e6; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 10px; }

p,
ul {
  margin: 0 0 20px; }

a {
  text-decoration: none;
  color: #39c; }

ul {
  padding-left: 20px; }

hr {
  border: 0;
  background: #e5e5e5;
  height: 1px;
  margin: 0 0 20px; }

textarea {
  resize: none; }

@media screen and (max-width: 720px) {
  body {
    word-wrap: break-word; } }

@media screen and (max-width: 480px) {
  body {
    padding: 15px; } }

